.f4square {
    min-width: 45px;
    min-height: 45px;
    max-width: 90px;
    max-height: 90px;
    width: 11vw; /* larghezza rispetto alla dimensione totale dello schermo*/
    height: 11vw;
    border-radius: 50%;
    border: solid blue;
    background-color: white;
  }


  .X{
    background-color: red;
  }
  
  .O{
    background-color: yellow;
  }

  .f4square:focus {
    outline: none;
  }
  